.app {
  display: flex;
  height: 100vh;
  font-family: 'Segoe UI', Arial, sans-serif;
  background-color: #f5f8fa;
}

/* Sidebar */
.sidebar {
  width: 250px;
  background-color: #349ccc;
  padding: 20px;
  border-right: 1px solid #d1d5d8;
  overflow-y: auto;
}

.sidebar h2 {
  font-size: 18px;
  margin-bottom: 20px;
  color: white;
  font-weight: bolder;
}

.field {
  margin-bottom: 20px;
}

.field label {
  display: block;
  margin-bottom: 5px;
  font-weight: 500;
  color: #e6f2f8;
}

.field textarea {
  width: 90%;
  padding: 8px;
  border: 1px solid #d1d5d8;
  border-radius: 4px;
  font-size: 14px;
  background-color: #f5f8fa;
  color: #004b7c;
  min-width: 90%;
  max-width: 90%;
  min-height: 50px;
  max-height: 500px;
}

.field input[type="range"] {
  width: 100%;
  -webkit-appearance: none; /* Remove default styling */
  background: transparent; /* Needed for Firefox */
}

/* Track */
.field input[type="range"]::-webkit-slider-runnable-track {
  height: 6px;
  background: #006cb5;
  border-radius: 3px;
}

/* Thumb */
.field input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  margin-top: -6px; /* Align thumb properly */
  width: 18px;
  height: 18px;
  background: #004b7c; /* Darker Dell Blue */
  border-radius: 50%;
  cursor: pointer;
}

/* Main Chat Area */
.main {
  flex: 1;
  display: flex;
  flex-direction: column;
  background-color: #f5f8fa;
  padding: 20px;
  overflow-y: auto;
}

h1 {
  font-size: 24px;
  margin-bottom: 20px;
  color: #007DB8;
}

/* Chat messages */
.chat-box {
  flex: 1;
  overflow-y: auto;
  background: #ffffff;
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 20px;
  border: 1px solid #d1d5d8;
}

.message {
  background: #f0f4f8;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 8px;
}

.message.bot {
  background-color: #d1ecff;
  margin-right: 20px;
  color: #004b7c;
}

.message.user {
  background-color: #e6f2f8;
  margin-left: 20px;
  text-align: right;
  color: #004b7c;
}

.markdown pre {
  background: #2d2d2d;
  color: #ffffff;
  padding: 10px;
  border-radius: 8px;
  overflow-x: auto;
}

/* Input area */
.input-area {
  display: flex;
  gap: 10px;
}

.input-area textarea {
  flex: 1;
  padding: 12px;
  font-size: 14px;
  border-radius: 20px;
  min-height: 10%;
  max-height: 60%;
  min-width: 50%;
  max-width: 90%;
  border: 1px solid #d1d5d8;
  background-color: #ffffff;
  color: #333333;
  resize: none !important;
  overflow: hidden;
}

.input-area textarea:focus {
  outline: none;
  border-color: #004b7c;
}

button {
  padding: 12px 20px;
  background-color: #007DB8;
  color: white;
  border: none;
  border-radius: 20px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #005e93;
}

button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

/* Loading text */
.loading {
  font-style: italic;
  color: #666666;
}

.login {
  text-align: center;
  margin-top: 100px;
}

.login h2 {
  color: #004b7c;
}

.login button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #4285F4;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.login button:hover {
  background-color: #2d6cd2;
}

.logout-button {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 8px 12px;
  background: #ef4444;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.logout-button:hover {
  background: #ca2727;
}
